body {
  background: url("https://images.unsplash.com/photo-1529753253655-470be9a42781?ixlib=rb-1.2.1&auto=format&fit=crop&w=800&q=60");
  color: beige;
  font-family: Georgia, "Times New Roman", Times, serif;
  margin: 0;
  padding: 0;
}
.container {
  width: 1000px;
  margin: 40px 40px 40px 40px;
  margin: 0;
  margin: 0 auto;
}
.movies {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 20px;
}

.movie {
  display: flex;
  max-width: 400px;
  min-height: 200px;
  padding: 10px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  background-color: rgba(0, 0, 0, 0.5);
  margin: 20px;
  border-radius: 20px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}
.movie:hover {
  transform: translate(0, -5%);
  transition: all 0.3s;
}
.genres {
  display: flex;
}

.movie img {
  width: 120px;
  height: 200px;
  padding: 5px;
}

.movie__title {
  position: relative;
  font-style: italic;
}

.movie__title {
  font-size: 22px;
  margin: 0;
  padding: 0;
}
.movie__year {
  font-size: 11px;
  margin: 10 0 0 0;
  padding: 0;
}
.genres {
  font-size: 12px;
  margin: 0;
  padding: 0;
  width: 10px;
}
.genres__genre {
  margin: 0;
  padding: 0;
}
.movie__summary {
  top: 20px;
  margin: 0;
  padding: 0;
  font-size: 10px;
  max-width: 290px;
  position: relative;
  left: 5px;
}

.happy {
  text-align: center;
  font-size: 40px;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}
